body {
  color: #666;
  background: #fff;
  font: 14px/1.429 "Open Sans", "Arial", "Helvetica", sans-serif;
  min-width: 320px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

img {
  max-width: 100%;
  height: auto;
  vertical-align: top; }

.gm-style img {
  max-width: none; }

a, button, input[type="submit"] {
  cursor: pointer;
  transition: color 0.2s ease, background-color 0.2s ease, opacity 0.2s ease, border-color 0.2s ease, visibility 0.2s ease, transform 0.2s ease;
  -webkit-transition: color 0.2s ease, background-color 0.2s ease, opacity 0.2s ease, border-color 0.2s ease, visibility 0.2s ease, -webkit-transform 0.2s ease; }

body, html {
  height: 100%; }

#wrapper {
  display: flex;
  flex-direction: column;
  position: relative;
  min-height: 100%;
  overflow: hidden; }

#main {
  flex-grow: 1;
  position: relative;
  z-index: 1;
  padding: 77px 0 0; }

.intro-slideshow {
  position: relative;
  height: 500px;
  color: #fff;
  text-align: center;
  background: #000; }
  .intro-slideshow:hover .bg {
    opacity: 0.95; }
  .intro-slideshow .slide {
    display: flex !important;
    align-items: center;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    min-height: 500px;
    padding: 50px 0;
    pointer-events: none; }
    .intro-slideshow .slide.active {
      z-index: 1;
      pointer-events: auto; }
  .intro-slideshow .bg {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 0.85;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: cover;
    transition: opacity 0.2s ease-in-out; }
  .intro-slideshow .btn-left,
  .intro-slideshow .btn-right {
    position: absolute;
    z-index: 2;
    left: 10px;
    top: 50%;
    width: 40px;
    height: 40px;
    overflow: hidden;
    margin: 0;
    padding: 0;
    text-indent: -9999px;
    border-radius: 0;
    border: 0;
    outline: 0;
    background: url(../images/arrow-left.svg) no-repeat 50% 50%;
    background-size: contain;
    transform: translate(0, -50%); }
    .intro-slideshow .btn-left:hover,
    .intro-slideshow .btn-right:hover {
      transform: translate(-5px, -50%) scale(1.1); }
    @media (max-width: 767px) {
      .intro-slideshow .btn-left,
      .intro-slideshow .btn-right {
        display: none; } }
  .intro-slideshow .btn-right {
    left: auto;
    right: 10px;
    background-image: url(../images/arrow-right.svg); }
    .intro-slideshow .btn-right:hover {
      transform: translate(5px, -50%) scale(1.1); }

.btn-theme-primary:hover {
  border-color: #01549b;
  background: #01549b; }

.feature-item {
  color: inherit; }
  .feature-item:hover {
    text-decoration: none; }
  .feature-item .fa-stack-1x {
    color: #fff; }
  .feature-item img.feature-icon {
    padding: 15px; }
  .feature-item h4 {
    margin: 0 0 30px; }

.section-about .about-image {
  box-shadow: 0 5px 8px rgba(0, 0, 0, 0.12); }
  .section-about .about-image img {
    margin: 0; }

.section-contact .address-contact > a {
  float: left;
  color: #aaa; }
  .section-contact .address-contact > a:hover {
    color: #03c4eb; }

#footer {
  flex-shrink: 0;
  padding: 25px 0 15px;
  color: #fff;
  background: #111; }
